@use '../size';

@keyframes zoom-in {
    0% {
        transform: scale(0);
        opacity: 0;
    }
    20% {
        opacity:0;
    }
    100% {
        transform: scale(1);
        opacity: 1;
    }
}

.modal__zoom-in {
    animation-name: zoom-in;
    animation-duration: 0.6s;
    animation-fill-mode: forwards;
}

@keyframes zoom-out {
    0% {
        transform: scale(1);
        opacity: 1;
    }
    50% {
        opacity: 0;
    }
    100% {
        transform: scale(0);
        opacity: 0;
    }
}

.modal__zoom-out {
    animation-name: zoom-out;
    animation-duration: 0.4s;
    animation-fill-mode: forwards;
}

@keyframes fade-in {
    from {
        background-color: rgba(0,0,0,0);
        visibility: visible;
    }
    to {
        background-color: rgba(0,0,0,0.6);
        visibility: visible;
    }
}

.modal__fade-in {
    animation-name: fade-in;
    animation-duration: 0.6s;
    animation-fill-mode: forwards;
}

@keyframes fade-out {
    from {
        background-color: rgba(0,0,0,0.6);
        visibility: visible;
    }
    to {
        background-color: rgba(0,0,0,0); 
        visibility: hidden;
    }
}

.modal__fade-out {
    animation-name: fade-out;
    animation-duration: 0.4s;
    animation-fill-mode: forwards;
}

.modal {
    display: flex;
    visibility: hidden;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: fixed;
    z-index: 999999;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0,0,0,0.6);
}

.modal__close-btn {
    display: none;
    color: var(--clr-text);
    width: size.$xl2;
    height: size.$xl2;
    margin-top: 2rem;
    margin-right: 2rem;
    align-self: flex-end;
}

.modal__content {
    margin: auto;
    display: block;
    //width: 90%;
    max-height: 90%;
    width: auto;
}

// xs
@media (min-width: 475px) {
    .modal__close-btn {
        width: size.$xl3;
        height: size.$xl3;
    }
}

// sm 
//@media (min-width: 640px) {}

// md
//@media (min-width: 768px) {}

// lg
@media (min-width: 1024px) {
    .modal__close-btn {
        width: size.$xl4;
        height: size.$xl4;
        margin-top: 4rem;
        margin-right: 4rem;
    }

    .modal__content {
        width: 80%;
    }
}

// xl
@media (min-width: 1280px) {
    .modal__close-btn {
        width: size.$xl5;
        height: size.$xl5;
        margin-top: 5rem;
        margin-right: 5rem;
    }

    .modal__content {
        // width: 70%;
        max-height: 90%;
        width: auto;
    }
}

// 2xl
@media (min-width: 1536px) {
    .modal__content {
        // width: 60%;
        max-height: 90%;
        width: auto;
    }
}
