@use '../size';

.about {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.about__content {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.about__title {
    font-size: size.$xl2;
    color: var(--clr-text);
}

.about__description {
    max-width: 60ch;
    font-size: size.$sm;
    color: var(--clr-text);
}

.about__hr {
    border-color: var(--clr-secondary);
}

.about__subtitle {
    font-size: size.$sm;
    color: var(--clr-text);
}

.about__ul-container {
    display: flex;
}

.about__ul {
    display: flex;
    flex-direction: column;
    gap: 1em;
    list-style-type: '\1F44D';
    padding-left: 1em;
    flex: 1;
}

.about__list {
    font-size: size.$sm;
    color: var(--clr-accent);

    &::before {
        content: '';
        margin-left: 0.5em;
    }
}

// xs
@media (min-width: 475px) {
    .about {
        gap: 1.5rem;
    }

    .about__content {
        gap: 1.5rem;
    }

    .about__title {
        font-size: size.$xl3;
    }

    .about__description {
        font-size: size.$base;
    }

    .about__subtitle {
        font-size: size.$base;
    }

    .about__list {
        font-size: size.$base;
    }
}

// sm 
@media (min-width: 640px) {
    .about__img-wrapper {
        margin: 0 auto;
    }

    .about__img {
        width: 28.6875rem;
    }
}

// md
//@media (min-width: 768px) {}

// lg
@media (min-width: 1024px) {
    .about {
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
    }

    .about__title {
        font-size: size.$xl4;
    }

    .about__description {
        font-size: size.$lg;
    }

    .about__subtitle {
        font-size: size.$lg;
    }

    .about__list {
        font-size: size.$lg;
    }
}

// xl
@media (min-width: 1280px) {
    .about__content {
        gap: 2rem;
    }
    
    .about__title {
        font-size: size.$xl5;
    }

    .about__description {
        font-size: size.$xl;
    }

    .about__subtitle {
        font-size: size.$xl;
    }

    .about__list {
        font-size: size.$xl;
    }

    .about__img {
        width: 30.75rem;
    }
}

